import Head from "next/head";
import React, { useEffect } from "react";
import { GlobalMenu } from "../../components/organisms/GlobalMenu/GlobalMenu";
import useInitTransition from "../../hooks/useInitTransition";
import { Menu } from "../../components/templates/Menu/Menu";
import useSetHeader from "../../hooks/useSetHeader";
import { CreateBgColor } from "../../containers";
import colors from "../../constants/colors";
import time from "../../constants/time";

/**
 * Styled component.
 */
export default function Page({
  setTransTime
}) {
  useInitTransition(setTransTime);
  useSetHeader({ color: "white" });

  return (
    <>
      <Head>
        <title>グローバルメニュー</title>
      </Head>
      <Menu>
        <div>
          <GlobalMenu setTransTime={ setTransTime } />
        </div>
      </Menu>
    </>
  )
}
